import { createRouter, createWebHistory,createWebHashHistory } from 'vue-router'
import install from "../components/install.vue";
import Home from '../views/Home.vue'
import menu from "../components/menu.vue";
import aafxButton from "../components/aafxButton.vue";
import aafxModal from "../components/aafxModal.vue";
import aafxSelector from "../components/aafxSelector.vue";
import aafxPopover from "../components/aafxPopover.vue";
import aafxTabs from "../components/aafxTabs.vue";
const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect:'/install',
    children:[
      {
        path:'/install',
        name:'install',
        component:install
      },
      {
        path:'/menu',
        name:'menu',
        component:menu
      },
      {
        path:'/fxButton',
        name:'fxButton',
        component:aafxButton
      },
      {
        path:'/fxModal',
        name:'fxModal',
        component:aafxModal
      },
      {
        path:'/fxSelector',
        name:'fxSelector',
        component:aafxSelector
      },
      {
        path: '/fxPopover',
        name: 'fxPopover',
        component: aafxPopover
      },
      {
        path: '/fxTabs',
        name: 'fxTabs',
        component: aafxTabs
      },

    ]
  },

]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
